/* MVP와 우승팀 나오는 div */
.sub_result {
    display: flex;
    width: 80%;
    height: max-content;
    margin-left: auto;
    margin-right: auto;
    margin-top: 10vh;
}
.mvpbox {
    width: 40%;
    height: 80vh;
}
